import React from 'react'
import {Heart} from '@pxblue/react-progress-icons';

export default function ScoreQuiz(props) {

    return (
        <div>
           <Heart percent={(props.score.toFixed(2) / (props.totalSteps.toFixed(2) - 1-0).toFixed(2)) * 100.0} 
           size={"%"} color={'#ff0000'} outlined={true}/>
 
        </div>
    )
}
