import React, { useState } from "react";

export default function TestChoose(props) {
  // console.log(props);

  const quiz = props.myquiz;
  const chooses = quiz.chooses;
  const [borderColor, setborderColor] = useState([props.totalSteps]);
  const [solidBorder, setsolidBorder] = useState("none");
  const clickHandler = (index) => {

    document.querySelector("#quiz"+quiz.trueChoose).style.borderStyle = "solid";
    document.querySelector("#quiz"+quiz.trueChoose).id = "";


    if (quiz.trueChoose !== index) {

      document.querySelector("#quiz"+index).style.borderStyle = "solid";
      document.querySelector("#quiz"+index).style.borderColor = "#ff0000";
      document.querySelector("#quiz"+index).id = "";
  
     

    }
    else{
      props.update();
    }
  
    setTimeout(() => { props.nextStep(); }, 500);

    
  };

  return (
    <div className="w-100">
      <div class="row wrapper border-bottom white-bg page-heading w-100">
        <div class="col">
          <h2>{quiz.title_q}</h2>
        </div>
      </div>

      <div className="wrapper wrapper-content animated fadeInRight">
        <div className="row">
          {chooses.map((choose, index) => (
            <div
              className="col-md-4 m-2"
              onClick={() => clickHandler(index)}
              style={{
                borderStyle: solidBorder[index],
                borderColor: "green",
                borderRadius: "5%",
              }}
              id={"quiz"+index}
            >
              <div className="ibox">
                <div className="ibox-content product-box">
                  <img
                    style={{ width: "100%", height: "100%" }}
                    src={choose.image_q}
                    alt=""
                  />
                  <div className="product-desc">
                    <a href="#" className="product-name">
                      {choose.title_q}
                    </a>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
      {/* <Stats step={2} {...props} /> */}
    </div>
  );
}
